import React, { useEffect, useState, useContext } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';
import Moment from 'react-moment';
import moment from 'moment'
import * as Yup from 'yup';
import io from "socket.io-client";


import InputLabel from "@material-ui/core/InputLabel";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import { useFileUpload } from 'use-file-upload'




import { HOST } from 'client/config.js'
import { useParams } from "react-router";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";
import ChatSidebar from "client/components/Chats/ChatSidebar.js";
import ChatTextBox from 'components/Chats/ChatTextBox'
import ImageModel from 'components/Chats/ImageModel'
import { LoginContext } from "client/index.js";
import { socket } from "client/layouts/Admin.js";



const styles = {
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0",
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none",
    },
};

const useStyles = makeStyles(styles);

export default function Chats() {
    const { id } = useParams();
    const history = useHistory();
    const { userData, setUserData } = React.useContext(LoginContext);
    console.log('userData', userData)


    const [messages, setMessages] = React.useState([])
    const [messageUser, setMessageUser] = React.useState([])
    const classes = useStyles();
    const [files, selectFiles] = useFileUpload();
    const [modelToggle, setModelToggle] = useState(false);
    const [receiverId, setReceiverId] = useState('617250b78418f505045b0e03');


    React.useEffect(() => {
        fetch(`${HOST}/api/admin`)
            .then(resp => resp.json())
            .then(resp => {
                setReceiverId(resp.data._id)
                fetch(`${HOST}/api/message?sender=${userData._id}&receiver=${resp.data._id}`)
                    .then(res => res.json())
                    .then(res => {
                        if (res.data) {
                            (res.data).forEach(ele => {
                                setMessages((pre) => [{ _id: ele.sender._id, name: ele.sender.name, text: ele.message, type: ele.type, file: ele.file }, ...pre]);
                            });
                        }
                })
            })

}, [userData]);

const downlodeHandler =(value)=>{
    fetch(value, {
      method: "GET",
      headers: {}
    })
      .then(response => {
        response.arrayBuffer().then(function(buffer) {
          const url = URL.createObjectURL(new Blob([buffer]));
          const link = document.createElement("a");
          link.href = url;
        //   React.createElement('a', {download: value.substr(value.lastIndexOf('/')), href: url}, null),
          link.setAttribute("download",value.substr(value.lastIndexOf('/'))); //or any other extension
          document.body.appendChild(link);
          link.click();
        });
      })
      .catch(err => {
        console.log(err);
      });
}
console.log('Messsage', messages);


React.useEffect(() => {
    socket.on("message", (data) => {
        console.log(data);
        let temp = messages;
        temp.push(data);
        setMessages((pre) => [...pre, data]);
        let tpmess = messageUser;
        if (tpmess.some(e => e._id !== data._id)) {
            console.log(data._id, tpmess, 'this');
            setMessageUser((pre) => [...tpmess, data]);
        }
    });
}, [socket]);




console.log(messageUser);


const sendSocketHandler = (value) => {
    console.log(value);
    const val = { text: value, user: userData, _id: userData._id, receiver: receiverId }
    socket.emit("chat", val);
}

const selectFileHandler = () => {
    selectFiles({ multiple: true }, (files) => {
        files.map(({ source, name, size, file }) => {
            console.log({ source, name, size, file })
        })
        setModelToggle(true);
    })
}
const sendfileHandler = () => {
    console.log('send Clicked');
    setModelToggle(false);
    const reader = new FileReader();
    reader.readAsDataURL(files[0].file);
    reader.onloadend = function (e) {
        console.log('clickbuffer', e.target.result);
        socket.emit('chat', { text: 'File', user: userData, _id: userData._id, receiver: receiverId, file: reader.result });
    };
    // reader.readAsArrayBuffer(files[0].file);
}



return (
    <div>
        <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
                <Card>
                    <CardHeader color="primary">
                        <h4 className={classes.cardTitleWhite}>Chats</h4>
                        <p className={classes.cardCategoryWhite}>Complete Chats</p>
                    </CardHeader>
                    <CardBody>
                        <GridContainer>
                            <GridItem xs={12} sm={12} md={12}>
                                <GridContainer xs={12} md={12}>
                                    <GridItem xs={4} md={4}>
                                        <ChatSidebar messagesUser={messageUser} />
                                    </GridItem>
                                    <GridItem xs={12} md={8}>
                                        <ChatTextBox
                                            sendSocketHandler={sendSocketHandler}
                                            messagesData={messages}
                                            role="user"
                                            selectFileHandler={selectFileHandler}
                                            downlodeHandler={downlodeHandler}
                                        />
                                        <ImageModel files={files} modelToggle={modelToggle} setModelToggle={setModelToggle} sendfileHandler={sendfileHandler} />
                                    </GridItem>
                                </GridContainer>
                            </GridItem>
                        </GridContainer>
                    </CardBody>
                </Card>
            </GridItem>

        </GridContainer>

    </div>
);
}

